*,
*::before,
*::after {
	box-sizing: inherit;
}

*::selection {
	color: $color-white;
	background: $color-blue-ribbon;
}

html {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

body {
	font-family: $font-family-SuisseIntl;
	-webkit-font-smoothing: antialiased;
	font-size: vmin(15);
	line-height: 1.6;
	text-decoration-skip: objects;
	text-rendering: optimizeLegibility;
	text-size-adjust: 100%;
	color: $color-cod-gray;
	-webkit-tap-highlight-color: transparent;

	.is-show-mobile-menu & {
		overflow: hidden;
	}

	@include desktop {
		min-width: vw(1260);
		font-size: vw(15);
	}

	@include desktop-xl {
		min-width: 1260px;
		font-size: 15px;
	}

	@include desktop-xxl {
		min-width: px(1260);
		font-size: px(15);
	}
}

button:not([disabled]),
a[role="button"]:not([disabled]) {
	cursor: pointer;
}

a {
	text-decoration: none;
	color: inherit;
	transition: $duration-normal;

	&.is-loading {
		position: relative;
		padding-left: vmin(30);
		pointer-events: none;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: vmin(-3);
			border: solid vmin(2) $color-blue-ribbon;
			border-bottom-color: rgba($color-blue-ribbon, 0.2);
			border-radius: 50%;
			width: vmin(18);
			height: vmin(18);
			animation: buttonLoading linear 1s infinite;

			@include desktop {
				top: vw(-3);
				border: solid vw(2) $color-blue-ribbon;
				border-bottom-color: rgba($color-blue-ribbon, 0.2);
				width: vw(18);
				height: vw(18);
			}

			@include desktop-xl {
				top: -3px;
				border: solid 2px $color-blue-ribbon;
				border-bottom-color: rgba($color-blue-ribbon, 0.2);
				width: 18px;
				height: 18px;
			}

			@include desktop-xxl {
				top: px(-3);
				border: solid px(2) $color-blue-ribbon;
				border-bottom-color: rgba($color-blue-ribbon, 0.2);
				width: px(18);
				height: px(18);
			}
		}

		@include desktop {
			padding-left: vw(30);
		}

		@include desktop-xl {
			padding-left: 30px;
		}

		@include desktop-xxl {
			padding-left: px(30);
		}
	}

	&.is-success {
		position: relative;
		padding-left: vmin(25);
		color: $color-mountain-meadow;
		pointer-events: none;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: vmin(-2);
			margin-top: vmin(-3);
			border-right: vmin(2) solid $color-mountain-meadow;
			border-bottom: vmin(2) solid $color-mountain-meadow;
			width: vmin(10);
			height: vmin(16);
			transform: rotate(45deg);

			@include desktop {
				top: vw(-2);
				margin-top: vw(-3);
				border-right: vw(2) solid $color-mountain-meadow;
				border-bottom: vw(2) solid $color-mountain-meadow;
				width: vw(10);
				height: vw(16);
			}

			@include desktop-xl {
				top: -2px;
				margin-top: -3px;
				border-right: 2px solid $color-mountain-meadow;
				border-bottom: 2px solid $color-mountain-meadow;
				width: 10px;
				height: 16px;
			}

			@include desktop-xxl {
				top: px(-2);
				margin-top: px(-3);
				border-right: px(2) solid $color-mountain-meadow;
				border-bottom: px(2) solid $color-mountain-meadow;
				width: px(10);
				height: px(16);
			}
		}

		@include desktop {
			padding-left: vw(25);
		}

		@include desktop-xl {
			padding-left: 25px;
		}

		@include desktop-xxl {
			padding-left: px(25);
		}
	}

	@include desktop {
		&:hover {
			color: $color-blue-ribbon;
		}
	}
}

img {
	vertical-align: middle;
	max-width: 100%;
}
