.is-hidden {
	// stylelint-disable-next-line declaration-no-important
	display: none !important;
}

.for-desktop {
	@include mobile {
		// stylelint-disable-next-line declaration-no-important
		display: none !important;
	}
}

.for-mobile {
	@include desktop {
		// stylelint-disable-next-line declaration-no-important
		display: none !important;
	}
}

.clearfix {
	overflow: auto;

	&::after {
		content: "";
		display: table;
		clear: both;
	}
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.fadeUp {
	&.anime {
		opacity: 0;
		transform: translateY(50px);
		transition: $duration-normal * 2;

		&.animated {
			opacity: 1;
			transform: translate(0);
		}
	}
}

.fadeDown {
	&.anime {
		opacity: 0;
		transform: translateY(-50px);
		transition: $duration-normal * 2;

		&.animated {
			opacity: 1;
			transform: translate(0);
		}
	}
}

.main {
	overflow: hidden;
}

.content {
	padding-top: vmin(90);
	padding-bottom: vmin(80);

	@include desktop {
		padding-top: vw(105);
		padding-bottom: vw(150);
		min-height: calc(100vh - #{vw(430)});
	}

	@include desktop-xl {
		padding-top: 105px;
		padding-bottom: 150px;
		min-height: calc(100vh - 430px);
	}

	@include desktop-xxl {
		padding-top: px(105);
		padding-bottom: px(150);
		min-height: calc(100vh - #{px(430)});
	}
}

.content--index {
	padding-top: vmin(100);

	@include desktop {
		padding-top: vw(120);
	}

	@include desktop-xl {
		padding-top: 120px;
	}

	@include desktop-xxl {
		padding-top: px(120);
	}
}

.content--404 {
	@include desktop {
		padding-top: vw(160);
		min-height: calc(100vh - 30vw);
	}

	@include desktop-xl {
		padding-top: 160px;
		min-height: calc(100vh - 430px);
	}

	@include desktop-xxl {
		padding-top: px(160);
		min-height: calc(100vh - #{px(430)});
	}
}

.container {
	margin: 0 auto;
	padding: 0 vmin(10);
	width: 100%;

	@include desktop {
		padding: 0 vw(15);
		max-width: vw(1278);
	}

	@include desktop-xl {
		padding: 0 15px;
		max-width: 1278px;
	}

	@include desktop-xxl {
		padding: 0 px(15);
		max-width: px(1278);
	}
}

.wrapper {
	&__side {
		@include desktop {
			flex-shrink: 0;
			align-self: flex-end;
			width: vw(424);
		}

		@include desktop-xl {
			width: 424px;
		}

		@include desktop-xxl {
			width: px(424);
		}
	}

	&__content {
		width: 100%;
	}

	@include desktop {
		display: flex;
	}
}

