.page-text {
	&.page-text-list {
		ul {
			margin: vmin(16) 0;

			@include desktop {
				margin: vw(16) 0;
			}

			@include desktop-xl {
				margin: 16px 0;
			}

			@include desktop-xxl {
				margin: px(16) 0;
			}
		}
	}

	a {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			bottom: -1px;
			width: 100%;
			height: 1px;
			background: rgba(0, 0, 0, 0.2);
			transition: $duration-normal;
		}

		@include desktop {
			&:hover {
				&::after {
					opacity: 0;
				}
			}
		}
	}
}

