}

.text-gradient {
	@include text-gradient;
}

.page-text-list {
	ul,
	ol {
		list-style-type: none;
		margin: 0;
		padding: 0;

		li {
			position: relative;
			padding-left: vmin(25);

			+ li {
				margin-top: vmin(12);

				@include desktop {
					margin-top: vw(8);
				}

				@include desktop-xl {
					margin-top: 8px;
				}

				@include desktop-xxl {
					margin-top: px(8);
				}
			}

			@include desktop {
				padding-left: vw(25);
			}

			@include desktop-xl {
				padding-left: 25px;
			}

			@include desktop-xxl {
				padding-left: px(25);
			}
		}

		ul,
		ol {
			margin-top: vmin(12);

			@include desktop {
				margin-top: vw(8);
			}

			@include desktop-xl {
				margin-top: 8px;
			}

			@include desktop-xxl {
				margin-top: px(8);
			}
		}
	}

	ul {
		li {
			position: relative;
			padding-left: vmin(25);

			&::before {
				content: "";
				position: absolute;
				left: 0;
				top: vmin(10);
				border-radius: 50%;
				width: vmin(5);
				height: vmin(5);
				background: $color-cod-gray;

				@include desktop {
					top: vw(10);
					width: vw(5);
					height: vw(5);
				}

				@include desktop-xl {
					top: 10px;
					width: 5px;
					height: 5px;
				}

				@include desktop-xxl {
					top: px(10);
					width: px(5);
					height: px(5);
				}
			}

			@include desktop {
				padding-left: vw(25);
			}

			@include desktop-xl {
				padding-left: 25px;
			}

			@include desktop-xxl {
				padding-left: px(25);
			}
		}
	}

	ol {
		counter-reset: list;

		li {
			&::before {
				counter-increment: list;
				content: counter(list) ". ";
				position: absolute;
				left: 0;
				top: 0;
			}
		}

		ol {
			counter-reset: sublist;

			li {
				&::before {
					counter-increment: sublist;
					content: counter(list) "." counter(sublist) ".";
				}
			}
		}
	}
}

