.link-text {
	position: relative;

	&::after {
		content: "";
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		background: rgba(0, 0, 0, 0.2);
		transition: $duration-normal;
	}

	@include desktop {
		&:hover {
			&::after {
				opacity: 0;
			}
		}
	}
}

.link-arrow {
	&::after {
		content: "";
		display: inline-block;
		vertical-align: middle;
		margin-top: vmin(-2);
		margin-left: vmin(10);
		border-top: solid vmin(2) currentColor;
		border-right: solid vmin(2) currentColor;
		width: vmin(8);
		height: vmin(8);
		transform: rotate(45deg);

		@include desktop {
			margin-top: vw(-2);
			margin-left: vw(10);
			border-top: solid vw(2) currentColor;
			border-right: solid vw(2) currentColor;
			width: vw(8);
			height: vw(8);
		}

		@include desktop-xl {
			margin-top: -2px;
			margin-left: 10px;
			border-top: solid 2px currentColor;
			border-right: solid 2px currentColor;
			width: 8px;
			height: 8px;
		}

		@include desktop-xxl {
			margin-top: px(-2);
			margin-left: px(10);
			border-top: solid px(2) currentColor;
			border-right: solid px(2) currentColor;
			width: px(8);
			height: px(8);
		}
	}

	@include desktop {
		&:hover {
			color: $color-denim;
		}
	}
}

.link-icon {
