		vertical-align: middle;
		margin: vmin(-2) vmin(8) 0 0;
		width: vmin(24);
		height: vmin(24);

		@include desktop {
			margin: vw(-2) vw(8) 0 0;
			width: vw(24);
			height: vw(24);
		}

		@include desktop-xl {
			margin: -2px 8px 0 0;
			width: 24px;
			height: 24px;
		}

		@include desktop-xxl {
			margin: px(-2) px(8) 0 0;
			width: px(24);
			height: px(24);
		}
	}
}

