.arrow-prev,
.arrow-next {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	outline: none;
	border: none;
	border-radius: 50%;
	padding: 0;
	width: vmin(40);
	height: vmin(40);
	background: $color-black;
	transition: $duration-normal;

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		width: vmin(20);
		height: vmin(20);
		fill: $color-white;
		transform: translate(-50%, -50%) rotate(180deg);

		@include desktop {
			width: vw(24);
			height: vw(24);
		}

		@include desktop-xl {
			width: 24px;
			height: 24px;
		}

		@include desktop-xxl {
			width: px(24);
			height: px(24);
		}
	}

	&:focus {
		border-radius: vmin(80);
		box-shadow: 0 0 0 vmin(2) $color-white, 0 0 0 vmin(4) $color-blue-ribbon;

		@include desktop {
			border-radius: vw(80);
			box-shadow: 0 0 0 vw(2) $color-white, 0 0 0 vw(4) $color-blue-ribbon;
		}

		@include desktop-xl {
			border-radius: 80px;
			box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color-blue-ribbon;
		}

		@include desktop-xxl {
			border-radius: px(80);
			box-shadow: 0 0 0 px(2) $color-white, 0 0 0 px(4) $color-blue-ribbon;
		}
	}

	&:disabled,
	&.is-disabled {
		border-color: $color-geyser;
		background: $color-geyser;
		pointer-events: none;

		svg {
			fill: $color-gull-gray;
		}
	}

	&.swiper-button-disabled {
		opacity: 0;
		visibility: hidden;
	}

	&.is-loading {
		position: relative;
		color: transparent;
		pointer-events: none;

		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 50%;
			margin: vmin(-9) 0 0 vmin(-9);
			border: solid vmin(2) $color-white;
			border-bottom-color: rgba($color-white, 0.2);
			border-radius: 50%;
			width: vmin(18);
			height: vmin(18);
			animation: buttonLoading linear 1s infinite;

			@include desktop {
				margin: vw(-9) 0 0 vw(-9);
				border: solid vw(2) $color-white;
				border-bottom-color: rgba($color-white, 0.2);
				width: vw(18);
				height: vw(18);
			}

			@include desktop-xl {
				margin: -9px 0 0 -9px;
				border: solid 2px $color-white;
				border-bottom-color: rgba($color-white, 0.2);
				width: 18px;
				height: 18px;
			}

			@include desktop-xxl {
				margin: px(-9) 0 0 px(-9);
				border: solid px(2) $color-white;
				border-bottom-color: rgba($color-white, 0.2);
				width: px(18);
				height: px(18);
			}
		}

		svg {
			opacity: 0;
			visibility: hidden;
		}
	}

	@include desktop {
		width: vw(56);
		height: vw(56);

		&:hover {
			transform: scale(1.15);
		}
	}

	@include desktop-xl {
		width: 56px;
		height: 56px;
	}

	@include desktop-xxl {
		width: px(56);
		height: px(56);
	}
}

