.map-pane {
	position: absolute;
	left: 0;
	bottom: vmin(49);
	border-radius: vmin(2);
	padding: vmin(20);
	width: vmin(230);
	box-shadow: 0 vmin(4) vmin(24) rgba(67, 72, 78, 0.2);
	box-sizing: border-box;
	background: $color-white;
	transform: translateX(-50%);

	&::after {
		content: "";
		position: absolute;
		left: 50%;
		top: 100%;
		border-top: solid vmin(14) $color-white;
		border-right: solid vmin(10) transparent;
		border-left: solid vmin(10) transparent;
		transform: translateX(-50%);

		@include desktop {
			border-top: solid vw(14) $color-white;
			border-right: solid vw(10) transparent;
			border-left: solid vw(10) transparent;
		}

		@include desktop-xl {
			border-top-width: 14px;
			border-right-width: 10px;
			border-left-width: 10px;
		}

		@include desktop-xxl {
			border-top-width: px(14);
			border-right-width: px(10);
			border-left-width: px(10);
		}
	}

	&__name {
		margin-bottom: vmin(4);
		font-weight: 600;
		font-size: vmin(14);
		line-height: 1.57;

		@include desktop {
			margin-bottom: vw(4);
			font-size: vw(14);
		}

		@include desktop-xl {
			margin-bottom: 4px;
			font-size: 14px;
		}

		@include desktop-xxl {
			margin-bottom: px(4);
			font-size: px(14);
		}
	}

	&__list {
		list-style-type: none;
		margin: 0;
		padding: 0;
		font-size: vmin(12);
		line-height: 1.5;

		li {
			+ li {
				margin-top: vmin(4);

				@include desktop {
					margin-top: vw(4);
				}

				@include desktop-xl {
					margin-top: 4px;
				}

				@include desktop-xxl {
					margin-top: px(4);
				}
			}
		}

		@include desktop {
			font-size: vw(12);
		}

		@include desktop-xl {
			font-size: 12px;
		}

		@include desktop-xxl {
			font-size: px(12);
		}
	}

	@include desktop {
		bottom: vw(49);
		border-radius: vw(2);
		padding: vw(24);
		width: vw(234);
		box-shadow: 0 vw(4) vw(24) rgba(67, 72, 78, 0.2);
	}

	@include desktop-xl {
		bottom: 49px;
		border-radius: 2px;
		padding: 24px;
		width: 234px;
		box-shadow: 0 4px 24px rgba(67, 72, 78, 0.2);
	}

	@include desktop-xxl {
		bottom: px(49);
		border-radius: px(2);
		padding: px(24);
		width: px(234);
		box-shadow: 0 px(4) px(24) rgba(67, 72, 78, 0.2);
	}
}

.page-content {
	&__wrapper {
		@include desktop {
			max-width: vw(612);
		}

		@include desktop-xl {
			max-width: 612px;
		}

		@include desktop-xxl {
			max-width: px(612);
		}
	}

	&__title {
		@include desktop {
			margin-bottom: vw(40);
		}

		@include desktop-xl {
			margin-bottom: 40px;
		}

		@include desktop-xxl {
			margin-bottom: px(40);
		}
	}

	.page-text-list {
		.h3 {
			margin-bottom: 0;
			font-weight: 400;
		}
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 vmin(-65) vmin(40);

		&__col {
			padding: 0 vmin(65);
			width: 100%;

			&:first-child {
				.page-content__item__image {
					text-align: right;
				}

				.page-content__item__content {
					margin-left: auto;
				}

				@include desktop {
					margin-top: vw(104);
				}

				@include desktop-xl {
					margin-top: 104px;
				}

				@include desktop-xxl {
					margin-top: px(104);
				}
			}

			& + & {
				@include mobile {
					margin-top: vmin(20);
				}
			}

			@include desktop {
				padding: 0 vw(65);
				width: auto;
			}

			@include desktop-xl {
				padding: 0 65px;
			}

			@include desktop-xxl {
				padding: 0 px(65);
			}
		}

		@include desktop {
			margin: vw(40) vw(-65) vw(80);
		}

		@include desktop-xl {
			margin: 40px -65px 80px;
		}

		@include desktop-xxl {
			margin: px(40) px(-65) px(80);
		}
	}

	&__item {
		&__image {
			margin-bottom: vmin(24);

			@include desktop {
				margin-bottom: vw(40);
			}

			@include desktop-xl {
				margin-bottom: 40px;
			}

			@include desktop-xxl {
				margin-bottom: px(40);
			}
		}

		&__content {
			@include desktop {
				max-width: vw(400);
			}

			@include desktop-xl {
				max-width: 400px;
			}

			@include desktop-xxl {
				max-width: px(400);
			}
		}

		&__image {
			img {
				width: 100%;

				@include desktop {
					width: vw(506);
				}

				@include desktop-xl {
					width: 506px;
				}

				@include desktop-xxl {
					width: px(506);
				}
			}
		}

		&__text {
			margin-bottom: vmin(16);

			@include desktop {
				margin-bottom: vw(16);
			}

			@include desktop-xl {
				margin-bottom: 16px;
			}

			@include desktop-xxl {
				margin-bottom: px(16);
			}
		}

		& + & {
			margin-top: vmin(36);

			@include desktop {
				margin-top: vw(104);
			}

			@include desktop-xl {
				margin-top: 104px;
			}

			@include desktop-xxl {
				margin-top: px(104);
			}
		}
	}

	&__item--big {
		.page-content__item__image {
			img {
				@include desktop {
					width: vw(612);
				}

				@include desktop-xl {
					width: 612px;
				}

				@include desktop-xxl {
					width: px(612);
				}
			}
		}
	}

	&__item--small {
		.page-content__item__image {
			img {
				@include desktop {
					width: vw(294);
				}

				@include desktop-xl {
					width: 294px;
				}

				@include desktop-xxl {
					width: px(294);
				}
			}
		}

		.page-content__item__content {
			@include desktop {
				max-width: vw(294);
			}

			@include desktop-xl {
				max-width: 294px;
			}

			@include desktop-xxl {
				max-width: px(294);
			}
		}
	}

	&__subtitle {
		margin: 0 0 vmin(16);
		font-weight: 400;

		@include desktop {
			margin: 0 0 vw(16);
		}

		@include desktop-xl {
			margin: 0 0 16px;
		}

		@include desktop-xxl {
			margin: 0 0 px(16);
		}
	}

	&__inner {
		display: flex;
		flex-wrap: wrap;
		margin: vmin(24) vmin(-12) 0;

		&__col {
			margin-bottom: vmin(24);
			padding: 0 vmin(12);
			width: 100%;

			@include desktop {
				margin-bottom: 0;
				padding: 0 vw(12);
				width: 50%;
			}

			@include desktop-xl {
				padding: 0 12px;
			}

			@include desktop-xxl {
				padding: 0 px(12);
			}
		}

		@include desktop {
			margin: vw(24) vw(-12) vw(40);
		}

		@include desktop-xl {
			margin: 24px -12px 40px;
		}

		@include desktop-xxl {
			margin: px(24) px(-12) px(40);
		}
	}

	&__block {
		padding: vmin(36) vmin(20);
		margin: 0 vmin(-10);
		background: $color-athens-gray;

		&__title {
			margin-bottom: 24px;
		}

		&__text {
			margin-bottom: 8px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		@include desktop {
			margin: 0;
			padding: vw(40);
		}

		@include desktop-xl {
			padding: 40px;
		}

		@include desktop-xxl {
			padding: px(40);
		}
	}
}

.fancybox-slide--html {
	.fancybox-close-small {
		right: vmin(24);
		top: vmin(24);
		padding: 0;
		width: vmin(24);
		height: vmin(24);
		color: $color-cod-gray;
		opacity: 1;
		transition: $duration-normal;

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -1px;
			width: 100%;
			height: vmin(2);
			background: currentColor;

			@include desktop {
				height: vw(2);
			}

			@include desktop-xl {
				height: 2px;
			}

			@include desktop-xxl {
				height: px(2);
			}
		}

		&::before {
			transform: rotate(45deg);
		}

		&::after {
			transform: rotate(-45deg);
		}

		svg {
			display: none;
		}

		@include desktop {
			right: vw(24);
			top: vw(24);
			width: vw(24);
			height: vw(24);

			&:hover {
				color: $color-denim;
			}
		}

		@include desktop-xl {
			right: 24px;
			top: 24px;
			width: 24px;
			height: 24px;
		}

		@include desktop-xxl {
			right: px(24);
			top: px(24);
			width: px(24);
			height: px(24);
		}
	}

	@include mobile {
		padding: 0;
	}
}

.fancybox-slide--video {
	.fancybox-content {
		background: $color-white;
	}
}

.up {
	position: fixed;
	right: vmin(15);
	bottom: vmin(15);
	z-index: 99;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	padding: 0;
	width: vmin(50);
	height: vmin(50);
	background: $color-black;
	opacity: 0;
	visibility: hidden;
	transition: $duration-normal;

	&::before {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		margin: vmin(-3) 0 0 vmin(-7);
		border-top: solid 2px $color-white;
		border-left: solid 2px $color-white;
		width: vmin(14);
		height: vmin(14);
		transform: rotate(45deg);

		@include desktop {
			margin: vw(-3) 0 0 vw(-7);
			width: vw(14);
			height: vw(14);
		}

		@include desktop-xl {
			margin: -3px 0 0 -7px;
			width: 14px;
			height: 14px;
		}

		@include desktop-xxl {
			margin: px(-3) 0 0 px(-7);
			width: px(14);
			height: px(14);
		}
	}

	&.is-show {
		opacity: 1;
		visibility: visible;
	}

	@include desktop {
		right: vw(30);
		bottom: vw(62);
		width: vw(64);
		height: vw(64);

		&:hover {
			background: $color-blue-ribbon;
		}
	}

	@include desktop-xl {
		right: 30px;
		bottom: 62px;
		width: 64px;
		height: 64px;
	}

	@include desktop-xxl {
		right: px(30);
		bottom: px(62);
		width: px(64);
		height: px(64);
	}
}

@keyframes buttonLoading {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}
